.App {
  text-align: center;
  background-color: rgb(69, 106, 187);
  width: auto;
  height: 1000px;
  font-size: 25px;
  padding: 150px;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 10px;
}

.App-header {
  background-color: #222;
  height: 10px;
  padding: 10px;
  color: rgb(92, 50, 50);
}

.App-intro {
  font-size: large;
}

@keyframes App-logo-spin {
  from { transform: rotate(0deg); }
  to { transform: rotate(360deg); }
}
.Forms
{
  text-align: center;
  background-color: rgb(2, 9, 12);
  font-size: 75px;

}


